import {Component, Input, OnInit} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import {FormControl, Validators} from '@angular/forms';
import { map, switchMap, debounceTime, tap, finalize } from 'rxjs/operators';
import { of } from 'rxjs';
const caster=require('angular-crud/gs-cast');
import { PaymentService } from '../payment.service';
import { Payment } from '../payment';

  import { ManagerService } from "src/app/params/manager/manager.service";
import { Manager} from "src/app/params/manager/manager";
import { ManagerFilter} from "src/app/params/manager/manager-filter";

  import { ProductCommandService } from "src/app/stock_operations/product_command/product-command.service";
import { ProductCommand} from "src/app/stock_operations/product_command/product-command";
import { ProductCommandFilter} from "src/app/stock_operations/product_command/product-command-filter";
import {AuthService} from "../../../params/manager/auth/auth.service";

@Component({
  selector: 'app-payment-edit',
  styleUrls:['./payment-edit.scss'],
  templateUrl: './payment-edit.component.html'
})
export class PaymentEditComponent implements OnInit {
id: string;
amountInput:FormControl=new FormControl();
dateInput:FormControl=new FormControl();
@Input()
product_command:ProductCommand;

payment: Payment;
  feedback: any = {};
constructor(
    private route: ActivatedRoute,
    private router: Router,
    private paymentService: PaymentService,
     private managerService:ManagerService,
     private authService: AuthService,  private product_commandService: ProductCommandService,
    )
    {
  }
ngOnInit() {

  this.payment=new Payment()   ;
    this.configureManagerInput()
    this.configureProductCommandInput()
  this.configureAmountInput();
    this.configureDateInput();
}



configureAmountInput(){

  let minAmount=1;
  let maxAmount=this.product_command.amount-this.product_command.amount_payment;

  this.payment.amount=maxAmount;
  this.amountInput.setValidators( [Validators.max(maxAmount), Validators.min(minAmount)])


}


configureDateInput(){

  this.payment.date=new Date();
}

save() {
  this.payment.product_command=this.product_command;
  this.payment.manager=this.authService.active_user;
    this.paymentService.save(this.payment).subscribe(
      payment => {
        this.payment = payment;
        this.feedback = {type: 'success', message: 'Payment effectu avec succs'};
        this.ngOnInit();
        setTimeout(() => {
          this.feedback={};
          ProductCommand.addPayment(payment,this.product_command);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: "Erreur lors de l'enregistrement du paiement : "+err.message};
      }
    );
  }
cancel() {
    this.router.navigate(['/stock_operations/payments']);
  }

filteredManagerList:Manager[]=new Array<Manager>();
  managerInput:FormControl;
  selectedManager:Manager;
  isLoadingManager=false;
managerClick(event: any) {
    this.selectedManager= event.option.value;
  }
checkManager() {
    if (!this.selectedManager|| this.selectedManager!== this.managerInput.value) {
      this.managerInput.setValue(null);
      this.selectedManager= null;
      return;
    }
    this.payment.manager=this.selectedManager;
  }
displayManager(manager:Manager) {
if (manager) return manager
}
  configureManagerInput(){
    this.managerInput=new FormControl();
this.managerInput.valueChanges
    .pipe(
      debounceTime(500),
      tap(() => {this.isLoadingManager= true;}),
      switchMap(value => this.managerService.find(new ManagerFilter())
      .pipe(
        finalize(() => this.isLoadingManager= false),
        )
        )
      )
    .subscribe((resultList =>{
      caster.arrayCast(resultList,Manager);
      this.filteredManagerList=resultList;}))
}

filteredProductCommandList:ProductCommand[]=new Array<ProductCommand>();
  product_commandInput:FormControl;
  selectedProductCommand:ProductCommand;
  isLoadingProductCommand=false;
product_commandClick(event: any) {
    this.selectedProductCommand= event.option.value;
  }
checkProductCommand() {
    if (!this.selectedProductCommand|| this.selectedProductCommand!== this.product_commandInput.value) {
      this.product_commandInput.setValue(null);
      this.selectedProductCommand= null;
      return;
    }
    this.payment.product_command=this.selectedProductCommand;
  }
displayProductCommand(product_command:ProductCommand) {
if (product_command) return product_command
}
  configureProductCommandInput(){
    this.product_commandInput=new FormControl();
this.product_commandInput.valueChanges
    .pipe(
      debounceTime(500),
      tap(() => {this.isLoadingProductCommand= true;}),
      switchMap(value => this.product_commandService.find(new ProductCommandFilter())
      .pipe(
        finalize(() => this.isLoadingProductCommand= false),
        )
        )
      )
    .subscribe((resultList =>{
      caster.arrayCast(resultList,ProductCommand);
      this.filteredProductCommandList=resultList;}))
}

}
